import React, { Component } from 'react'
import {randomWord} from './words'
import './Drawman.css'
import img0 from "./img0.png";
import img1 from "./img1.png";
import img2 from "./img2.png";
import img3 from "./img3.png";
import img4 from "./img4.png";
import img5 from "./img5.png";
import img6 from "./img6.png";


export default class Drawman extends Component {
      /** by default, allow 6 guesses and use provided gallows images. */
  static defaultProps = {
    maxWrong: 6,
    images: [img0, img1, img2, img3, img4, img5, img6]
  };

    constructor(props){
        super(props)
     this.state = { nWrong: 0, guessed: new Set(), answer: randomWord() };
     this.handleGuess = this.handleGuess.bind(this);
     this.rePlay = this.rePlay.bind(this)
   }
 
   /** guessedWord: show current-state of word:
     if guessed letters are {a,p,e}, show "app_e" for "apple"
   */
   guessedWord() {
     return this.state.answer
       .split("")
       .map(ltr => (this.state.guessed.has(ltr) ? ltr : "_"));
   }
 
   /** handleGuest: handle a guessed letter:
     - add to guessed letters
     - if not in answer, increase number-wrong guesses
   */
   handleGuess(evt) {
     let ltr = evt.target.value;
     this.setState(st => ({
       guessed: st.guessed.add(ltr),
       nWrong: st.nWrong + (st.answer.includes(ltr) ? 0 : 1)
     }));
   }
 
   /** generateButtons: return array of letter buttons to render */
   generateButtons() {
     return "abcdefghijklmnopqrstuvwxyz".split("").map((ltr, i) => (
       <button
         value={ltr}
         onClick={this.handleGuess}
         disabled={this.state.guessed.has(ltr)}
         key={i}
       >
         {ltr}
       </button>
     ));
   }
   rePlay() {
     this.setState({ nWrong: 0, guessed: new Set(), answer: randomWord() })
   }

   /** render: render game */
   render() {
     let gameOver = this.props.maxWrong <= this.state.nWrong
     return (
       <div  className='Drawman'>
          {this.guessedWord().join('') === this.state.answer 
          ? <div>
             <h1>Congrats!</h1>
                      <img className='Drawman-img' src={this.props.images[this.state.nWrong]} alt={`${this.state.nWrong} guesses out of 6`}/>

             <h1>Thats Right: {this.state.answer}</h1>
             <h2>You win!</h2>
             <button className='Drawman-reset' onClick={this.rePlay}>Play again</button>
           </div>
 
      :  <div>
         <h1>Draw the Man</h1>
         <img className='Drawman-img' src={this.props.images[this.state.nWrong]} alt={`${this.state.nWrong} guesses out of 6`}/>
         <p>{gameOver ? `The correct answer is: ` : `Wrong guesses: ${this.state.nWrong}`}</p>
   
         <p className='Drawman-word'>{gameOver ? this.state.answer : this.guessedWord()}</p>
         <p className='Drawman-btns'>
           {gameOver
           ? `Sorry, you lose.`
          : this.generateButtons()}
         </p>
         <button className='Drawman-reset' onClick={this.rePlay}>{gameOver ? 'Play Again' : 'Reset'}</button>
            </div>}
       
 
       </div>
     );
   }
 }
 
 
